/* Css for Featured */
.featured {
  width: 100%;
  max-width: 1024px;
  display: flex;
  justify-content: space-between;
  gap: 20px;
  z-index: 1;
}

/* Set Position and Atribute */
.featuredItem {
  position: relative;
  color: white;
  border-radius: 10px;
  overflow: hidden;
  height: 250px;
  flex: 1;
}

/* Set Atribute for IMG */
.featuredImg {
  width: 100%;
  height: 100%;
  object-fit: cover;
  cursor: pointer;
}

/* Set Atribute for Title */
.featuredTitles {
  position: absolute;
  bottom: 20px;
  left: 20px;
}
